<template>
  <div id="app">
    <div class="container">
      <h3 class="title">Güneş Enerjisi Fiyat Hesaplama | UDORA Teknoloji Demo</h3>
      <div :class="[KonumVisible ? 'harita' : 'hide']">
        <div>
          <gmap-map :center="center" :zoom="12" style="width:900px;  height: 500px;">
            <gmap-marker
              :position="marker"
              :clickable="true"
              :draggable="true"
              @dragend="getMarkerPosition($event)"
            ></gmap-marker>
          </gmap-map>
          <br />
          <p>
            Seçilen Koordinat :
            {{markerSelected.lat}} - {{markerSelected.lng}}
          </p>
        </div>
        <input v-on:click="koordinatiSec" class="btn" type="button" value="Konumu Seç" />
      </div>
      <div :class="[ParametreVisible ? 'parameter' : 'hide']">
        <div class="input-field">
          <div class="input">
            <label for="cati-alani">Çatı Alanını Giriniz :</label>
            <input v-model="CatiAlani" type="number" id="cati-alani" />
            <label for="cati-alani">
              m
              <sup>2</sup>
            </label>
          </div>
          <p
            class="maks-kapasite dangerTxt"
          >Maksimum Kapasite : {{MaxKapasite ===0 ? "Çatı Alanına Eksik" :MaxKapasite }}</p>
        </div>
        <div class="aylar row">
          <div class="guc-input col-12">
            <div :key="ay.id" v-for="ay in Aylar" class="input">
              <label for="cati-alani">{{ay.name}}:</label>
              <input v-model="ay.value" type="number" id="cati-alani" />
              <label for="cati-alani">kWh</label>
            </div>
          </div>

          <div class="col-12">
            <h3
              class="optimum-kapasite"
            >Optimum Güç : {{OptimumGüc ? OptimumGüc :"Tüm Aylar İcin Giris Yapınız"}}</h3>
            <div class="input">
              <label for="cati-alani">Kapasite Giriniz :</label>
              <input v-model="GirilenKapasite" type="number" id="cati-alani" />
            </div>

            <input v-on:click="konumuDuzenle" class="btn" type="button" value="Konumu Düzenle" />

            <input
              v-on:click="yatirimTutariHesapla"
              class="btn"
              type="button"
              value="Yatırım Tutarı Hesapla"
            />

            <h4 v-if="MaxKapasiteUyari" class="danger">Uyarı : Max Kapasiteden Yüksek olamaz</h4>
          </div>
        </div>
      </div>
      <div :class="[FaturaVisible ? 'fatura col' : 'hide']">
        <h4 class="ft">Optimum Güç : {{OptimumGüc ? OptimumGüc :"Aylara göre tüketim girilmedi!"}}</h4>
        <h4 class="ft" >Kapasite : {{GirilenKapasite ? GirilenKapasite :"Kapasite girişi yapılmadı!"}} kWh</h4>
        <h4 class="ft" >Konum :  {{markerSelected.lat}} - {{markerSelected.lng}}</h4>
        <h4 class="ft" >
          Çatı Alanı : {{CatiAlani ? CatiAlani :"Çatı alani girişi yapılmadı."}} m
          <sup>2</sup>
        </h4>
        <h3 class="ft">Yatırım Tutarı : {{YatirimTutari ? YatirimTutari : "Kapasite girişi yapılmadı!"}} $</h3>
        <div class="btn-grp col">
          <input v-on:click="duzenle" class="btn" type="button" value="Düzenle" />
          <input onclick="window.print();return false;" class="btn" type="button" value="Yazdır" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: "App",

  data() {
    return {
      KonumVisible: true,
      FaturaVisible: false,
      ParametreVisible: false,
      MaxKapasiteUyari: false,
      CatiAlani: 0,
      MaxKapasite: 0,
      GirilenKapasite: 0,
      OptimumGüc: 0,
      YatirimTutari: 0,
      KurulumAdresi: "turkey",
      ApiKey: "AIzaSyABZSbMpg8ovwyrJMHpA38nTJPZdxlrdy4",
      center: { lat: 41.015137, lng: 28.97953 },
      markers: [],
      places: [],
      marker: { lat: 41.015137, lng: 28.97953 },
      currentPlace: null,
      coordinates: null,
      markerSelected: {},
      Aylar: [
        { id: 1, name: "Ocak", value: Number },
        { id: 2, name: "Şubt", value: Number },
        { id: 3, name: "Mart", value: Number },
        { id: 4, name: "Nisn", value: Number },
        { id: 5, name: "Mays", value: Number },
        { id: 6, name: "Hzrn", value: Number },
        { id: 7, name: "Tmmz", value: Number },
        { id: 8, name: "Ağts", value: Number },
        { id: 9, name: "Eyll", value: Number },
        { id: 10, name: "Ekim", value: Number },
        { id: 11, name: "Kasm", value: Number },
        { id: 12, name: "Arlk", value: Number }
      ]
    };
  },
   mounted() {
    this.geolocate();
  },

 
  methods: {
    maxKapasiteCalc: function() {
      this.MaxKapasite = parseFloat(
        ((this.CatiAlani / (1.665 * 0.99)) * 0.9 * 320) / 1000
      );
    },
    optimumGucCalc: function() {
      let toplam = 0;
      this.Aylar.forEach(ay => {
        toplam = toplam + parseFloat(ay.value);
      });
      console.log(toplam);
      this.OptimumGüc = parseFloat((toplam / 12) * 0.6);
    },
    kapasiteKontrol: function() {
      if (this.GirilenKapasite > this.MaxKapasite) {
        this.MaxKapasiteUyari = true;
      } else {
        this.MaxKapasiteUyari = false;
      }
    },
    yatirimTutariHesapla: function() {
      //alert("Yatırım Hesapla")

      let carpan = 0;
      let X = this.GirilenKapasite;
      if (1 <= X * (X < 10)) {
        carpan = 0.0042 * X * X - 0.0911 * X + 1.3617;
      }
      if (10 <= X * (X < 65)) {
        carpan = -0.104 * Math.log(X) + 1.0993;
      }
      if (65 <= X * (X < 350)) {
        carpan = -0.06 * Math.log(X) + 0.9382;
      }
      if (350 <= X * (X < 1500)) {
        carpan = -0.048 * Math.log(X) + 0.8516;
      }
      if (1500 <= X * (X < 2100)) {
        carpan = -0.061 * Math.log(X) + 0.9497;
      }
      if (2100 <= X * (X < 8000)) {
        carpan = -0.032 * Math.log(X) + 0.7236;
      }
      if (8000 <= X * (X < 20000)) {
        carpan = -0.026 * Math.log(X) + 0.6741;
      }
      if (20000 <= X * (X <= 40000)) {
        carpan = -0.011 * Math.log(X) + 0.5188;
      }

      this.YatirimTutari = parseInt(carpan * X * 1000);
      this.ParametreVisible = false;
      this.FaturaVisible = true;
    },
    duzenle: function() {
      this.FaturaVisible = false;
      this.ParametreVisible = true;
    },
    koordinatiSec: function() {
      this.KonumVisible = false;
      this.ParametreVisible = true;
    },
    konumuDuzenle: function() {
      this.KonumVisible = true;
      this.ParametreVisible = false;
    },
    setPlace(place) {
      this.currentPlace = place;
    },
    addMarker() {
      if (this.currentPlace) {
        const marker = {
          lat: this.currentPlace.geometry.location.lat(),
          lng: this.currentPlace.geometry.location.lng()
        };
        this.markers.push({ position: marker });
        this.places.push(this.currentPlace);
        this.center = marker;
        this.currentPlace = null;
      }
    },
    geolocate: function() {
      navigator.geolocation.getCurrentPosition(position => {
        this.center = {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        };
      });
    },
    updateCoordinates(location) {
      this.coordinates = {
        lat: location.latLng.lat(),
        lng: location.latLng.lng()
      };
    },
    getMarkerPosition(event) {
      this.markerSelected = {
        lat: event.latLng.lat(),
        lng: event.latLng.lng()
      };
    }
  },

  updated: function() {
    this.maxKapasiteCalc();
    this.optimumGucCalc();
    this.kapasiteKontrol();
  }
};
</script>

